<template>
    <div>
        <div class="template">
            <h1></h1>
            <div>
                <h3></h3>
                <h5></h5>
                <input type="text" v-model="msg">
                <p>
                     {{  }} data
                    {{ msg }}</p>
                <p v-once>
                     v-once 
                    {{ msg }}
                </p>
                <h5>HTML</h5>
                <p v-html="rawHtml"></p>
            </div>
            <div>
                <h3></h3>
            </div>
            <div>
                <h3></h3>
            </div>
        </div>
        <div class="computed">
            <h1></h1>
            <p>
                <input type="text" v-model="question">
                <br>
                question: {{ question }} <br>
                new question: {{ newQuestion }} <br>
                answer: {{ answer }}
            </p>
        </div>
        <div class="if">
            <h1></h1>
            <p>
                <button @click="toggleSeen">toggle</button>
                <span v-if="seen"></span>
            </p>
        </div>
    </div>
</template>

<script>
export default {
    name: 'v-template',
    data() {
        return {
            msg: '',
            rawHtml: '<span style="color: red;">html</span>',
            question: '',
            answer: '',
            seen: true
        }
    },
    methods: {
        getAnswer(width = 10){
            this.answer = '' + width;
            this.answer = `${width}`
        },
        toggleSeen(){
            this.seen = !this.seen;
        }
    },
    computed: {
        newQuestion(){
            return this.question.split('').reverse().join('');
        }
    },
    watch: {
        question: function(newValue, oldValue){
            this.getAnswer(20);
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
    font-weight: normal;
}

ul {
    list-style-type: none;
    padding: 0;
}

li {
    display: inline-block;
    margin: 0 10px;
}

a {
    color: #42b983;
}
</style>



